<template>
    <div class="type-nav">
            <div class="container">
                <div @mouseenter="enterNav" @mouseleave="leaveNav">
                    <h2 class="all"></h2>
                    <!--  -->
                    <transition name="sort">
                        <div v-show="isShow" class="sort">
                        <div class="all-sort-list2" @click="goSearch">
                            <div class="item" v-for="(c1,index) in categoryList" :key="c1.categoryid" >
                                <h3 @mouseenter="indexInter(index)" :class="{current:currentIndex==index,animate__bounceIn:currentIndex==index}">  
                                    <!--    -->
                                    <a :data-categoryName='c1.categoryName' :data-category1Id='c1.categoryId'>{{c1.categoryName}}</a>
                                </h3>
                                <!--  -->
                                <!-- js -->
                                <div class="item-list clearfix" :style="{display:currentIndex==index?'block':'none'}">
                                    <div class="subitem">
                                        <dl class="fore" v-for="c2 in c1.categoryChild" :key="c2.categoryId">
                                            <dt>
                                                <a :data-categoryName='c2.categoryName' :data-category2Id='c2.categoryId'>{{c2.categoryName}}</a>
                                            </dt>
                                            <dd>
                                                <em v-for="c3 in c2.categoryChild" :key="c3.categoryId">
                                                    <a :data-categoryName='c3.categoryName' :data-category3Id='c3.categoryId'>{{c3.categoryName}}</a>
                                                </em>
                                            </dd>
                                        </dl>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div> 
                    </transition>
                </div>
                <nav class="nav">
                    <a href="###"></a>
                    <a href="###"></a>
                    <a href="###"></a>
                    <a href="###"></a>
                    <a href="###"></a>
                    <a href="###"></a>
                    <a href="###"></a>
                    <a href="###"></a>
                </nav>
            </div>
        </div>
</template>

<script>
import { mapState } from 'vuex'
import throttle from 'lodash/throttle'
export default {
    name:'TypeNav',
    data() {
        return {
            currentIndex:-1,  //
            isShow:true       //typeNav
        }
    },
    methods:{
        indexInter:throttle(function(index){  //,50ms
            console.log(index)
            this.currentIndex = index
        },50),
        enterNav(){
            if(this.$route.path !== '/home'){   //homeisShowfalse
            this.isShow = true
            }
        },
        leaveNav(){
            this.currentIndex = -1                //
            if(this.$route.path !== '/home'){   //homeisShowfalse
            this.isShow = false
            }
        },
        goSearch(event){
            // +
            // 
            // 
            // aaadldta
            // 
            // adata-categoryName
            let element = event.target
            // data-categoryNamea
            // dataset
            console.log(element.dataset)
            let{categoryname,category1id,category2id,category3id} = element.dataset
            //   :data-xxxx:'xxxx'vue
            if(categoryname){
                let location = {name:'search'}
                let query = {categoryName:categoryname}
                if(category1id){
                    query.category1id = category1id
                }else if(category2id){
                    query.category2id = category2id
                }else{
                    query.category3id = category3id
                }
                // params
                if(this.$route.params){
                    location.params = this.$route.params
                }
                console.log(query)
                location.query = query
                this.$router.push(location)
            }
        },
    },
    computed:{
        ...mapState({
            // value
            categoryList:(state)=>state.home.categoryList
        })
    },
    mounted(){
        if(this.$route.path !== '/home'){   //homeisShowfalse
            this.isShow = false
        }
    }
}
</script>

<style scoped lang="less">
    .type-nav {
        border-bottom: 2px solid #e1251b;

        .container {
            width: 1200px;
            margin: 0 auto;
            display: flex;
            position: relative;

            .all {
                width: 210px;
                height: 45px;
                background-color: #e1251b;
                line-height: 45px;
                text-align: center;
                color: #fff;
                font-size: 14px;
                font-weight: bold;
            }

            .nav {
                a {
                    height: 45px;
                    margin: 0 22px;
                    line-height: 45px;
                    font-size: 16px;
                    color: #333;
                }
            }

            .sort {
                position: absolute;
                left: 0;
                top: 45px;
                width: 210px;
                height: 461px;
                position: absolute;
                background: #fafafa;
                z-index: 999;

                .all-sort-list2 {
                    .item {
                        h3 {
                            line-height: 30px;
                            font-size: 14px;
                            font-weight: 400;
                            overflow: hidden;
                            padding: 0 20px;
                            margin: 0;

                            a {
                                color: #333;
                            }
                        }

                        .item-list {
                            display: none;
                            position: absolute;
                            width: 734px;
                            min-height: 460px;
                            background: #f7f7f7;
                            left: 210px;
                            border: 1px solid #ddd;
                            top: 0px;
                            z-index: 9999 !important;

                            .subitem {
                                float: left;
                                width: 650px;
                                padding: 0 4px 0 8px;

                                dl {
                                    border-top: 1px solid #eee;
                                    padding: 6px 0;
                                    overflow: hidden;
                                    zoom: 1;

                                    &.fore {
                                        border-top: 0;
                                    }

                                    dt {
                                        float: left;
                                        width: 54px;
                                        line-height: 22px;
                                        text-align: right;
                                        padding: 3px 6px 0 0;
                                        font-weight: 700;
                                    }

                                    dd {
                                        float: left;
                                        width: 415px;
                                        padding: 3px 0 0;
                                        overflow: hidden;

                                        em {
                                            float: left;
                                            height: 14px;
                                            line-height: 14px;
                                            padding: 0 8px;
                                            margin-top: 5px;
                                            border-left: 1px solid #ccc;
                                        }
                                    }
                                }
                            }
                        }

                        // &:hover {
                        //     .item-list {
                        //         display: block;
                        //     }
                        // }
                    }
                    .current {
                            background-color: skyblue;
                    }
                }
            }
            //  
            .sort-enter{
                height: 0px;
                opacity: 0;
            }
            .sort-enter-to{
                height: 461px;
                opacity: 100%;
            }
            // 
            .sort-enter-active{
                transition: 0.5s linear ;
            }
        }
    }
</style>