import { requestGetCode,requestUserLogin,requestUserRegister, requsetGetUserInfo, requsetUserLogout } from "@/api"
import { getToken,removeToken,setToken } from "@/utils/token"

const actions = {
    // 
    async getCodeNum({commit},phone){
        let result = await requestGetCode(phone)
        console.log(result)
        if(result.code === 200){
            commit('GETCODENUM',result.data)
        }else{
            return Promise.reject(new Error(result.message))
        }
    },
    // 
    // token
    async sendUserRegister({commit},user){
        let result = await requestUserRegister(user)
        console.log(result)
    },
    //  tokentoken
    async userLogin({commit},data){
        let result = await requestUserLogin(data)
        if(result.code == 200){
            commit('USERLOGIN',result.data.token)
            // token  
            setToken(result.data.token)
            console.log('222')
            return 'ok'
        }else{
            return Promise.reject(new Error(result.message))
        }
    },
    //   headerstoken
    async getUserInfo({commit}){
        let result = await requsetGetUserInfo()
        if(result.code === 200){
            commit('GETUSERINFO',result.data)
            return 'ok'
        }else{
            return Promise.reject(new Error(result.message))
        }
    },
    // 
    async userLogout({commit}){
        let result = await requsetUserLogout()
        if(result.code === 200){
            removeToken()
            commit('CLEAR')
            return 'ok'
        }else{
            return Promise.reject(new Error(result.message))
        }
    }
}
const mutations = {
    GETCODENUM(state,value){
        state.codeNum = value
    },
    USERLOGIN(state,value){
        // 
        state.token = value
        console.log('111')
    },
    GETUSERINFO(state,value){
        state.userInfoList = value
    },
    CLEAR(state){
        state.token = ''
        state.userInfoList = ''
    }
}
const state = {
    codeNum:'',
    // actioncommitgetTokenunderfined
    // commitstatetokentoken
    // tokengetTokentokenaction
    token:getToken('TOKEN'),
    userInfoList:{}
}
const getters = {}
export default {
    actions,
    mutations,
    state,
    getters
}