import Vue from 'vue'

import VueRouter from 'vue-router'

import Home from '@/pages/Home'

import Login from '@/pages/Login'

import Search from '@/pages/Search'

import Register from '@/pages/Register'

import Detail from '@/pages/Detail'

import AddCartSuccess from '@/pages/AddCartSuccess'

import ShopCart from '@/pages/ShopCart'

import Trade from '@/pages/Trade'

import Pay from '@/pages/Pay'

import PaySuccess from '@/pages/PaySuccess'

import Center from '@/pages/Center'

// Token
import store from '@/store'

// 
import MyOrder from '@/pages/Center/myOrder'

import GroupOrder from '@/pages/Center/groupOrder'

Vue.use(VueRouter)

// pushreplaceNavigationDuplicated
// pushreplace  
let originPush = VueRouter.prototype.push
let originReplace = VueRouter.prototype.replace

//     push
VueRouter.prototype.push = function (location, resolve, reject) {
    if (resolve && reject) {
        originPush.call(this, location, resolve, reject)
        // console.log(this)
    } else {
        originPush.call(this, location, () => { }, () => { })
        // console.log(this)
    }
}

VueRouter.prototype.replace = function (location, resolve, reject) {
    if (resolve && reject) {
        originReplace.call(this, location, resolve, reject)
        // console.log(this)
    } else {
        originReplace.call(this, location, () => { }, () => { })
        // console.log(this)
    }
}

let router = new VueRouter({
    routes: [
        {
            name: 'center',
            path: '/center',
            component: Center,
            redirect:'center/myOrder',
            children: [
                {
                    name: 'myOrder',
                    path: 'myOrder',
                    component: MyOrder
                },
                {
                    name: 'groupOrder',
                    path: 'groupOrder',
                    component: GroupOrder,
                }
            ]
        },
        {
            name: 'paysuccess',
            path: '/paysuccess',
            component: PaySuccess
        },
        {
            name: 'pay',
            path: '/pay',
            component: Pay
        },
        {
            name: 'trade',
            path: '/trade',
            component: Trade,
        },
        {
            name: 'shopcart',
            path: '/shopcart',
            component: ShopCart,
        },
        {
            name: 'addcartsuccess',
            path: '/addcartsuccess',
            component: AddCartSuccess,
        },
        {
            path: '/detail/:goodId?',
            component: Detail,
            meta: {
                isShow: true
            }
        },
        {
            path: '/home',
            component: Home,
            meta: {
                isShow: true
            }
        },
        {
            path: '/login',
            component: Login,
            meta: {
                isShow: false
            }
        },
        {
            name: 'search',
            path: '/search/:keyword?', //params
            component: Search,
            meta: {
                isShow: true
            }
        },
        {
            path: '/register',
            component: Register,
            meta: {
                isShow: false
            }
        },
        // /homehome
        {
            path: '*',
            redirect: '/home'
        }

    ],
    // 
    scrollBehavior(to, from, savePosition) {
        return { y: 0 }
    }
})

// 
//   
router.beforeEach(async (to, from, next) => {
    let token = store.state.user.token
    let name = store.state.user.userInfoList.name
    // // login
    // token&&to.path=='/login'&&next('/home')
    // // 
    // next()
    // // 
    if (token) {
        if (to.path == '/login') {
            next('/home')
        } else {
            // 
            // name
            if (name) {  //token
                next()
            } else {   // 
                try {
                    await store.dispatch('getUserInfo')
                    next()
                } catch (error) {
                    //  token
                    store.dispatch('userLogout')
                    //  token
                    next('/login')
                }
            }
        }
    } else {
        //  paypaysuccess
        // 
        let toPath = to.path
        console.log(toPath)
        if(toPath.indexOf('/trade')!=-1||toPath.indexOf('/pay')!=-1||toPath.indexOf('/center')!=-1||toPath.indexOf('/shop')!=-1){
            next('login?redirect='+toPath)
            // 
        }
        else{
            next()
        }
    }
})

export default router