import { requestDetailList } from "@/api"
import { reqAddOrUpdataShopCart } from "@/api"
import { getUUID } from "@/utils/uuid"

const actions = {
    async getDetailList({commit},skuId){
        let result = await requestDetailList(skuId)
        console.log(result)
        if(result.code === 200){
            commit('GETDETAILLIST',result.data)
        }
    },
    async addOrUpdataShopCart({commit},{skuId,skuNum}){
        let result = await reqAddOrUpdataShopCart(skuId,skuNum)
        console.log(result)
            if(result.code === 200){
                return 'ok'
            }
            else{
                return Promise.reject(new Error('fault'))
                // vuex
            }
    }
}
const mutations = {
    GETDETAILLIST(state,value){
        state.detailList = value
    },
    // ADDORUPDATASHOPCART(state,value){

    // }
}
const state = {
    detailList:{},
    uuid_token:getUUID()
}
const getters = {
    // 
    categoryView(state){
        //detailListajaxdetailListunderfined
        // detailList.xxx(underfined.xxx),
        // 
        // return{}
        // 
        return state.detailList.categoryView || {}
    },
    // 
    skuInfo(state){
        return state.detailList.skuInfo || {}
    },
    // 
    spuSaleAttrList(state){
        return state.detailList.spuSaleAttrList || {}
    }
}
export default { actions, mutations, state, getters }